.Links .changeParameters {
    position: relative;
    height: 350px;
    width: 100%;
    max-width: 350px;
    background-color: #672a96;
    border: solid 2px #622991;
    border-radius: 10px;
    text-align: center;
    font-size: xx-large;
    color: #fafef0;
    font-family: "Leelawadee", Arial, sans-serif;
    text-decoration: none;
    padding: 70px;
}

.Links .changeParameters:hover {
    background-color: #855abe;
    border: solid 2px #775abe;
}

