.Filters .navbar {
    background-color: #f9a811;
    box-shadow: 0 1px 14px 0 rgba(249, 168, 17, 0.5);
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: .5rem 1rem;
}

.ContentFilters {
    padding-top: 30px;
}

.Filters .HeaderFilters {
    position: relative;
    color: #fafef0;
    font-size: 1rem;
    margin-left: 20px;
}

.Filters .Brand {
    border-right: 1px solid #e19711;
    padding: 0 16px;
}

.Filters .navbar-brand {
    padding-left: 20px;
    color: white;
}

.Filters .Back .back-icon {
    padding-right: 30px;
    border-right: 1px solid #e19711;
}

.Filters .Back .navbar-brand {
    display: inline-block;
    margin-right: 1rem;
    font-size: 1.25rem;
    line-height: inherit;
    white-space: nowrap;
    color: white;
}

.Filters .Back:hover {
    color: #ffe2b1;
    text-decoration: none;
}

.Filters .form-control-file {
    color: #fafef0;
}

.Back {
    padding-right: 20px;
    color: #fafef0;
    text-decoration: none;
    height: 65px;
}